@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&family=Roboto+Slab:wght@100..900&display=swap);
:root {
  /*colors*/
  --white-color: #f5f5f7;
  --dark-white-color: #9C9C9C;
  --black-color: #1f1f1f;
  --warm-black: #0B0C10;
  --light-grey-color: #444445;
  --neon-color: #66FCF1;
  --grey-color: #3b3b3b;
  --dark-grey-color: #181818;

  /* fonts */
  --font-64: 64px;
  --font-56: 56px;
  --font-48: 48px;
  --font-36: 36px;
  --font-32: 32px;
  --font-28: 28px;
  --font-26: 26px;
  --font-24: 24px;
  --font-22: 22px;
  --font-20: 20px;
  --font-18: 18px;
  --font-16: 16px;
  --font-14: 14px;
  --font-12: 12px;

  /*font weight*/
  --font-weight-300: 300;
  --font-weight-400: 400;
  --font-weight-700: 700;

  /*line height*/
  --line-height-25: 25px;
  --line-height-33: 33px;
  --line-height-36: 36px;
  --line-height-38: 38px;
  --line-height-40: 40px;
  --line-height-30: 30px;
  --line-height-17: 17.5px;
  --line-height-19: 19px;

  /*font*/
  --font-roboto-slab: "Roboto Slab", serif;
  --font-lato: "Lato", sans-serif;

  /*border-radius*/
  --border-radius: 4px;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--black-color);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-lato);
}

p {
  font-family: var(--font-roboto-slab);
}

li {
  list-style-type: none;
}

footer {
  background-color: var(--dark-grey-color);
}

.Footer_footer-container__2hiID {
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 40px 0;
}

.Footer_copyright__RxHLz {
  font-size: var(--font-14);
  color: var(--dark-white-color);
}

.Footer_contact__3J3Ao ul {
  display: flex;
  grid-gap: 16px;
  gap: 16px;
  padding: 0;
  margin: 0;
}

.Footer_contact__3J3Ao ul li a {
  text-decoration: none;
  color: var(--dark-white-color);
  width: 50px;
  height: 50px;
  border: 1px solid rgba(248, 248, 248, .2);
  border-radius: 50%;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  transition: all .2s;
  display: flex;
}

.Footer_contact__3J3Ao ul li a:hover {
  color: var(--neon-color);
  background-color: #3b3b3b;
  border-color: rgba(0, 0, 0, 0);
}

@media (max-width: 500px) {
  .Footer_footer-container__2hiID {
    flex-direction: column-reverse;
    grid-gap: 40px;
    gap: 40px;
  }
}
.Header_header__CuJBR {
  position: fixed;
  top: 0;
  width: 100%;
  margin: 15px 0;
  transition: margin 0.3s ease-in, background-color 0.3s ease-in;
}

.Header_header-container__3iAY6 {
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
}

.Header_header__CuJBR.Header_sticky__1vyXz {
  margin: 0;
  background-color: var(--dark-grey-color);
}

.Header_logo-container__33-jI a {
  display: flex;
  grid-gap: 8px;
  gap: 8px;
}

nav {
  display: flex;
  align-items: center;
  grid-gap: 30px;
  gap: 30px;
}

nav ul {
  display: flex;
  grid-gap: 30px;
  gap: 30px;
  padding: 0;
}

header a {
  text-decoration: none;
  color: var(--white-color);
  font-family: var(--font-lato);
  font-size: var(--font-16);
}

.Header_navbar-container__2zV5b a {
  font-size: var(--font-14);
  position: relative;
  transition: color 0.3s ease-in-out;
}

.Header_navbar-container__2zV5b li a::before {
  content: "";
  position: absolute;
  top: 100%;
  width: 100%;
  height: 2.5px;
  background-color: var(--neon-color);
  transform: scaleX(0);
  transition: transform 0.3s ease-in-out;
}

.Header_navbar-container__2zV5b li a:hover {
  color: var(--dark-white-color);
}

.Header_navbar-container__2zV5b li a:hover::before {
  transform: scaleX(1);
}

.Header_button__6iMad {
  color: var(--neon-color);
  text-decoration: none;
}

.Header_hamburger-menu__3KyE0 {
  display: none;
}

@media (max-width: 768px) {

  .Header_header__CuJBR.Header_sticky__1vyXz,
  .Header_header__CuJBR {
    margin: 0;
    padding: 15px 0;
    background-color: var(--dark-grey-color);
  }

  .Header_hamburger-menu__3KyE0 {
    display: block;
  }

  .Header_hamburger-menu__3KyE0 span {
    height: 2px;
    position: relative;
    display: block;
    width: 18px;
    background-color: var(--white-color);
  }

  .Header_hamburger-menu__3KyE0 span:first-of-type {
    bottom: 3px;
    transition: transform 0.3s ease;
  }

  .Header_hamburger-menu__3KyE0 span:last-of-type {
    top: 3px;
    transition: transform 0.3s ease;
  }

  .Header_hamburger-menu__3KyE0.Header_opened__22AeR span:first-of-type {
    transform: rotate(45deg);
    bottom: -2px;
  }

  .Header_hamburger-menu__3KyE0.Header_opened__22AeR span:nth-last-of-type(2) {
    display: none;
  }

  .Header_hamburger-menu__3KyE0.Header_opened__22AeR span:last-of-type {
    transform: rotate(-45deg);
    top: 0px;
  }

  .Header_navbar-container__2zV5b {
    position: absolute;
    top: 50px;
    width: 100%;
    left: 0;
    display: none;
    background-color: var(--dark-grey-color);
    animation: Header_fadeSlideOutTop__3o-UD 0.5s ease;

  }

  .Header_navbar-container__2zV5b.Header_show-menu__37OvS {
    display: block;
    animation: Header_fadeSlideInTop__amRJg 0.5s ease;
  }

  nav {
    flex-direction: column;
    margin-bottom: 30px;
  }

  nav ul {
    flex-direction: column;
    margin-bottom: 0;
  }

  nav ul li {
    text-align: center;
  }
}

@keyframes Header_fadeSlideInTop__amRJg {
  0% {
    opacity: 0;
    transform: translateY(-100%);
  }

  60% {
    opacity: 0;
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes Header_fadeSlideOutTop__3o-UD {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}
.Button_button__JYKoP {
  border: 1px solid var(--neon-color);
  border-radius: var(--border-radius);
  padding: 10px 13px;
  font-family: var(--font-lato);
  line-height: 1;
  text-decoration: none;
  font-size: var(--font-14);
  width: -webkit-max-content;
  width: max-content;
  height: -webkit-max-content;
  height: max-content;
  cursor: pointer;
  transition: all 0.3s ease-in;
}

.Button_button__JYKoP:hover {
  background-color: var(--neon-color);
}

.Button_button__JYKoP:hover a {
  color: var(--black-color);
}
.Cursor_custom-cursor__3GkIH {
  position: fixed;
  pointer-events: none;
  z-index: 9999;
  transition: transform 0.15s ease-out;
  transform: translate(-50%, -50%);
}

.Cursor_custom-cursor__3GkIH svg {
  width: 20px;
  height: 20px;
  fill: none;
  stroke: var(--neon-color);
  stroke-width: 1;
  stroke-linecap: round;
  stroke-linejoin: round;
}
